import "./popup.scss";

const Popup = ({ content, setDisplay }: any) => {
    const handleClose = () => {
        setDisplay(false)
    };
    return (
        <div id="POPUP">
            <div className="button__close" onClick={handleClose}>
                Close X
            </div>
            {content}
        </div>
    );
};

export default Popup;
