import { useState } from "react";
import { LogoLight } from "../../assets";
import {
    Contact,
    Legal,
    displayContactContext,
    displayLegalContext,
} from "../..";
import "./navbar.scss";

const Navbar = () => {
    const [displayLegal, setDisplayLegal] = useState(false);
    const [displayContact, setDisplayContact] = useState(false);

    const handleDisplay = (prop: string) => {
        setDisplayContact(false);
        setDisplayLegal(false);
        if (prop === "legal") setDisplayLegal(true);
        if (prop === "contact") setDisplayContact(true);
    };

    return (
        <nav id="NAVBAR">
            <div className="logo">
                <LogoLight />
            </div>

            <div className="contact">
                <span onClick={() => handleDisplay("contact")}>contact</span>
                {displayContact && (
                    <displayContactContext.Provider value={setDisplayContact}>
                        <Contact />
                    </displayContactContext.Provider>
                )}
            </div>
            <div className="legal">
                <span onClick={() => handleDisplay("legal")}>legal</span>

                {displayLegal && (
                    <displayLegalContext.Provider value={setDisplayLegal}>
                        <Legal />
                    </displayLegalContext.Provider>
                )}
            </div>
        </nav>
    );
};
export default Navbar;
